.contact-ul-details {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 10px;
}

.contact-lists {
  list-style-type: none;
  color: white !important;
}
.links {
  color: white;
  text-decoration: none;
  font-size: 21px;
}
.links:hover {
  color: white;
  text-decoration: underline;
}

.icons {
  margin-right: 10px;
}

.icons:hover {
  list-style-type: none;
}
